.points {
  position: absolute;
  bottom: 50px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.points:hover {
}
.point {
  padding: 10px;
  margin-left: 5px;
  cursor: pointer;
  border-radius: 50%;
  opacity: 0.7;
}
.point:hover {
  opacity: 1;
}
